<template>
  <v-container>
    <v-row justify="center" align="center">
      <v-col cols="12" sm="8" md="6">
        <v-card width="400" class="mx-auto mt-10 px-2" color="#f2dda8" light>
          <v-card-title
            >Войдите в свой аккаунт чтобы получить доступ к сайту</v-card-title
          >
          <v-card-text>
            <v-form @submit.prevent="login">
              <v-text-field
                v-model="username"
                label="Логин"
                prepend-icon="mdi-account-circle"
              />
              <v-text-field
                v-model="password"
                :type="showPassword ? 'text' : 'password'"
                label="Пароль"
                prepend-icon="mdi-lock"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="showPassword = !showPassword"
              />
            </v-form>
          </v-card-text>
          <v-divider />
          <v-card-actions>
            <v-btn @click.prevent="login" text>Войти</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import NProgress from 'nprogress'
export default {
  name: 'Login',
  data() {
    return {
      showPassword: false,
      username: '',
      password: ''
    }
  },
  methods: {
    login() {
      NProgress.start()
      this.$store
        .dispatch('auth/login', {
          username: this.username,
          password: this.password
        })
        .then(() => {
          this.$router.push({ name: 'active-orders' })
        })
        .catch(error => {
          NProgress.done()
          throw error
        })
    }
  }
}
</script>

<style scoped>
.v-card__title {
  word-break: normal;
}
</style>
